.allCompanyWrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin-top: 20px;
}

.oneCompanyWrapper {
    min-width: 250px;
    height: 172px;
    border: 1px solid #ADD2DD;
    flex-basis: 30%;
    cursor: pointer;
    box-shadow: none;
    transition: 0.3;
    margin-bottom: 34px;
}

.oneCompanyWrapperSkeleton {
    min-width: 250px;
    height: 172px;
    flex-basis: 30%;
    cursor: pointer;
    box-shadow: none;
    transition: 0.3;
    margin-bottom: 34px;
}

.oneCompanyWrapper:hover {
    box-shadow: 0 4px 12px rgba(149,157,165,.2);
}

.oneCompanyName {
    font-family: 'SimpleMono-Medium';
    font-size: 14px;
    position: absolute;
    top: 63px;
    left: 85px;
}

.oneCompanyLogoWrapper {
    position: relative;
    margin-left: 10px;
    margin-top: 10px;
    min-width: 200px;
}

.oneCompanyLogo {
    width: 75px;
    height: 75px;
    object-fit: contain;
    border: 1px solid #ADD2DD;
}

.oneCompanyScoreWrapper {
    position: absolute;
    top: 0px;
    right: 10px;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background: #ADD2DD;
    display: flex; /* or inline-flex */
    align-items: center; 
    justify-content: center;
}

.oneCompanyScoreWrapperEmpty {
    position: absolute;
    top: 0px;
    right: 10px;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    border: 1px solid #BAB7BD;
    display: flex; /* or inline-flex */
    align-items: center; 
    justify-content: center;
}

.oneCompanyScore {
    font-family: 'Druk Bold Regular';
    font-size: 30px;
    margin-right: 10px;
}

.oneCompanyScoreEmpty {
    font-family: 'Druk Bold Regular';
    font-size: 30px;
}

.oneCompanyKeyInfoWrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    font-family: 'SimpleMono-Regular';
    font-size: 14px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 25px;
    text-align: center;
}

.oneCompanyLink { 
    text-decoration: none; /* no underline */
    color: inherit;
}

.noCompanies {
    font-family: 'SimpleNorm-Regular';
    width: 100%;
    border: 1px solid #ADD2DD;
    height: 500px;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (max-width: 768px) {
    .allCompanyWrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }

    .oneCompanyWrapper {
        width: 100%;
        height: 172px;
        border: 1px solid #ADD2DD;
        flex-basis: 100%;
        padding-bottom: 10px;
        margin-bottom: 10px;
    }
}